import SPPermission from '../SPPermission';

const assert: Chai.AssertStatic = chai.assert;

describe('SPPermission tests', () => {
  it('Fails to create Permission Objects with an invalid value', () => {
    /* tslint:disable:no-unused-expression */
    assert.throws(() => { new SPPermission(undefined); });
    /* tslint:disable:no-null-keyword */
    assert.throws(() => { new SPPermission(null); });
    /* tslint:enable:no-null-keyword */
    /* tslint:enable:no-unused-expression */
  });

  it('Fails to find permissions that are not present', () => {
    assert.isFalse(SPPermission.emptyMask.hasAnyPermissions(
      SPPermission.addAndCustomizePages,
      SPPermission.createSSCSite,
      SPPermission.manageSubwebs
    ));
    assert.isFalse(SPPermission.emptyMask.hasAllPermissions(
      SPPermission.addAndCustomizePages,
      SPPermission.createSSCSite,
      SPPermission.manageSubwebs
    ));
  });

  it('Find permissions that are present', () => {
    assert.isTrue(SPPermission.fullMask.hasAnyPermissions(
      SPPermission.addAndCustomizePages,
      SPPermission.createSSCSite,
      SPPermission.manageSubwebs
    ));
    assert.isTrue(SPPermission.fullMask.hasAllPermissions(
      SPPermission.addAndCustomizePages,
      SPPermission.createSSCSite,
      SPPermission.manageSubwebs
    ));
  });
});
